/*
$size-4: $size-default;
$size-3: $size-default * 2 - 8px;
$size-2: $size-default * 3 - 8px;
$size-1: $size-default * 4 - 8px;
*/
* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: #515251;
  font-family: 'Bitter',serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: #853977;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  line-height: 1;
}

/*

h1,h2,h3,h4,h5,h6
{
  margin-top:0;
  margin-bottom: $space-1;
  padding:0;
}


h1 { font-size: $sm-size-1; }
h2 { font-size: $sm-size-2; }
h3 { font-size: $sm-size-3; }
h4 { font-size: $sm-size-4; }
h5 { font-size: $sm-size-5; }
h6 { font-size: $sm-size-6; }
*/
header {
  width: 100%;
  z-index: 100;
  background-color: #fcfbfa;
  top: 0px;
  border-bottom: 1px solid #ccc;
  border-top: 10px solid #853977;
  padding: 25px 0;
}

header .container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.logo {
  display: block;
  width: 250px;
  height: 50px;
}

.logo span {
  background: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  width: inherit;
  height: 100%;
  width: 100%;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.menu-toggle {
  padding: 10px;
  cursor: pointer;
}

.menu-toggle > div {
  display: flex;
  height: 19px;
  justify-content: space-between;
  flex-direction: column;
  width: 28px;
}

.menu-toggle span {
  height: 3px;
  width: 28px;
  display: block;
  background: #853977;
  border-radius: 6px;
}

@media (min-width: 980px) {
  .menu-toggle {
    display: none;
  }
}

nav {
  display: none;
  height: 100%;
  font-family: 'Dosis',sans-serif;
  font-size: 22px;
  text-transform: lowercase;
}

nav ul {
  display: flex;
  margin: 0 0 0 30px;
  height: 100%;
  padding: 0;
  list-style: none;
}

nav li {
  display: flex;
  align-items: center;
}

nav a {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 20px 20px 0 20px;
  line-height: 1;
  height: 100%;
  color: #979599;
}

nav a.active {
  color: #3fa208;
}

@media (min-width: 980px) {
  nav {
    display: block;
  }
}

.container {
  margin: 0 auto;
  max-width: 1065px;
  width: 90%;
}

section {
  padding: 40px 0;
}

.image-container {
  font-size: 0;
  line-height: 1;
}

.image-container img {
  line-height: 1;
}

.translucent {
  opacity: 0.85;
}

.dosis {
  font-family: 'Dosis',sans-serif;
}

.bitter {
  font-family: 'Bitter',sans-serif;
}
